.who header {
    display: flex;
    flex-direction: row;
    min-height: 80vh;
    background-image: url('../../.././assets/images/mw.png');
    background-repeat: no-repeat;
    background-position: 85% bottom;
    background-size: 70vmin;
}


@media all and (max-width: 1024px) {
    .who header {
        background-size: 60vmin;
    }
}


@media all and (max-width: 520px) {
    .who header {
        background-position: center bottom;
    }
}

.who header div {
    width: 70vw;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 70vh;
}


@media all and (max-width: 520px) {
    .who header div {
        justify-content: normal;
    }
}

.who header h1 {
    color: #3B2D98;
    padding: 0;
    margin: 0;
    font-weight: 700;

}


@media all and (max-width: 520px) {
    .who header h1 {
        width: 100%;
        margin-top: 20vmin;
    }
}

.bgdarkblue {
    background-color: #342885;
    padding: 6vh 0 6vh 0;
    margin: 0;
}

.who header div p {
    width: 50%;
    font-weight: bold;
    color: #9CBAD8;
    font-size: calc(16px + 1vmin);
    line-height: calc(18px + 2vmin);

}


@media all and (max-width: 520px) {
    .who header div p {
        width: 100%;
    }
}

section.idea p.description {
    color: white;
    font-size: calc(14px + 2vmin);
    line-height: calc(16px + 4vmin);
    text-align: center;
}

section.idea p a {
    color: #E59767;
    text-decoration: none;
}

section.idea a:visited {
    color: #E59767;
    text-decoration: none;
}

section.idea a:hover {
    transition: background-color 0.2s ease-in-out 0s; 
    color: #E67B3A;
}


section.idea ul {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
}


@media all and (max-width: 768px) {
    section.idea ul {
        flex-direction: column;
    }
}

section.idea ul li {
    margin: 0;
    list-style: none;
    padding: 3vmin;
    font-size: 2px;
}


@media all and (max-width: 768px) {

    section.idea ul li {
        padding: 0vmin;
    }

}

section.idea ul li p {
    color: white;
    font-size: calc(12px + 1vmin);
    line-height: calc(16px + 2vmin);
    text-align: left;
}