*,
*:before,
*:after {
    box-sizing: border-box;
}

body {
    font-family: 'Roboto Slab', sans-serif;
    font-size: 14px;
}

.app {
    position: relative;
}

header {
    min-height: 100vh;
    background-image: url('../../.././assets/images/illustration.png');
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: 80vmin;
}


@media all and (max-width: 1024px) {
    header {
        background-size: 60vmin;
    }
}


/*@media all and (max-width: 1690px) { ... }*/
/*@media all and (max-width: 1280px) { ... }*/
/*@media all and (max-width: 980px) { ... }*/
/*@media all and (max-width: 736px) { ... }*/
/*@media all and (max-width: 480px) { ... }*/


header div.wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 100vh;
}


@media all and (max-width: 520px) {
    header div.wrapper {
        justify-content: normal;
    }
}


h1 {
    color: #3B2D98;
    padding: 0;
    margin: 0;
    width: 40vw;
    font-weight: 700;
    font-size: calc(16px + 8vmin);
    line-height: calc(18px + 6vmin);
}


@media all and (max-width: 740px) {
    h1 {
        width: 90%;
        margin-top: 15vmin;
    }
}

@media all and (max-width: 520px) {
    h1 {
        width: 100%;
        margin-top: 20vmin;
    }
}

header p {
    width: 30vw;
    padding: 1vw 0 1vw 0;
    color: #4A4A4A;
    font-size: calc(16px + 1vmin);
    line-height: calc(18px + 2vmin);
}


@media all and (max-width: 740px) {
    header p {
        width: 70%;
    }
}

@media all and (max-width: 520px) {
    header p {
        width: 90%;
    }
}

h1 span {
    color: #9CBAD8;
}


header a {
    border-radius: 50px;
    text-align: center;
    text-decoration: none;
    width: calc(100px + 8vw);
    background-color: #E59767;
    padding: 2vmin;
    color: white;
    cursor: pointer;
    font-weight: 600;
    font-size: calc(12px + 1vw);
}

header a:hover {
    transition: background-color 0.2s ease-in-out 0s; 
    background-color: #E67B3A;
}

.bgblue {
    background-color: #4636B3;
    padding: 8vmin 0 8vmin 0;
    margin: 0;
}

div.wrapper {
    width: 70vw;
    max-width: 1200px;
    margin: 0 auto 0 auto;
    justify-content: center;
}

p.subtitle {
    font-size: calc(12px + 1vmin);
    margin: 0;
    padding: 0;
}

h2 {
    color: #3B2D98;
    padding: 0;
    margin-top: 0;
    font-size: calc(18px + 3vmin);
}

.principles p.subtitle {
    color: #9CBAD8;
}

.principles h2 {
    color: #3B2D98;
}

.principles ul {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
}


@media all and (max-width: 740px) {
    .principles ul {
        flex-direction: column;
    }
}


.principles ul li {
    margin: 0;
    list-style: none;
    padding: 4vmin 2vmin 2vmin 2vmin;
    text-align: center;
}

.principles ul li h4 {
    color: #3B2D98;
    font-weight: 600;
    font-size: calc(10px + 2vmin);
    margin: 2vmin 0 0 0;
}

.principles ul li p {
    color: #4D4D4D;
    font-size: calc(12px + 1vmin);
    line-height: calc(16px + 2vmin);
}

.prototypes {
    margin-top: 6vh;
}

.prototypes h2 {
    color: white;
    padding: 0;
    margin: 0;
}

.prototypes p.subtitle {
    color: #9CBAD8;
}


div.prototype div.wrapper {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
}



@media all and (max-width: 740px) {
    div.prototype div.wrapper {
        flex-direction: column;
    }
}


div.prototype div.wrapper div {
    max-width: 33vw;
    padding-right: 3vw;
    margin: 0;
}


@media all and (max-width: 740px) {
    div.prototype div.wrapper div {
        max-width: 100%;
        padding-right: 0;
    }
}

div.prototype {
    padding: 6vh 0 6vh 0;
}

p.week {
    color: #9CBAD8;
    font-weight: bold;
    font-size: calc(12px + 2vmin);
}

h3 {
    padding: 0;
    margin: 0;
    font-size: calc(16px + 2vmin);
}

div.prototype p.desc {
    font-size: calc(12px + 1vmin);
    line-height: calc(16px + 2vmin);
}


.white {
    background-color: white;
}

.white h3 {
    color: #3B2D98;
}

.white p.desc {
    color: #4A4A4A;
}

.dark {
    background-color: #342885;
}

.dark h3 {
    color: white;
}

.dark p.desc {
    color: #FAFAFA;
}

a.button-link {
    margin-top: 8vmin;
    border-radius: 80px;
    border: none;
    display: block;
    text-align: center;
    text-decoration: none;
    width: calc(150px + 10vmin);
    background-color: #E59767;
    padding: 2vmin 3vmin 2vmin 4vmin;
    color: white;
    cursor: pointer;
    font-weight: 600;
    font-size: calc(10px + 1vmin);
    background-image: url('../../.././assets/images/github_icon.svg');
    background-repeat: no-repeat;
    background-position: 2vmin center;
    background-size: auto;
}

a.button-link:hover {
    transition: background-color 0.2s ease-in-out 0s; 
    background-color: #E67B3A;
 }

div.prototype img {
    background-color: #f4f4f4;
    -webkit-box-shadow: 0px 1px 16px 0px rgba(22, 22, 22, 0.2);
    -moz-box-shadow: 0px 1px 16px 0px rgba(22, 22, 22, 0.2);
    box-shadow: 0px 1px 16px 0px rgba(22, 22, 22, 0.2);
    border: 0;
}


@media all and (max-width: 740px) {

    a.button-link {
        padding: 3vmin 3vmin 3vmin 4vmin;
    }

    div.prototype img {
        margin: 10vmin auto 0 auto;
        width: 100%;
        height: auto;
    }

}

footer {
    background-color: #4636B3;
    padding: 3vh 0 3vh 0;
}

footer div ul {
    margin: 0 0 0 1vw;
    padding: 0;
    display: inline-flex;
}

footer div ul li {
    display: block;
    list-style: none;
    color: #C4BCFA;
    font-size: calc(8px + 1vmin);
    margin: 0 0 0 calc(8px + 1vmin);
    align-self: center;

}

footer div div {
    display: inline-flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
}

footer div img {
    align-self: center;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    opacity: 0.9;
}

@media all and (max-width: 800px) {

    footer div div {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
    }

    footer div ul {
        margin: 0 0 0 1vw;
        padding: 0;
        display: inline-block;

    }


    footer div ul li {
        display: inline-block;
        list-style: none;

        color: #C4BCFA;
        font-size: calc(8px + 1vmin);
        margin: 0 0 0 0;


    }

    footer div ul li:last-child {
        display: block;
        width: 100%;
        text-align: center;
        list-style: none;
        color: #C4BCFA;
        font-size: calc(8px + 1vmin);
        margin: 0 0 0 0;


    }

}


li.twitter a {
    height: 50px;
    width: 50px;
    display: block;
    background-image: url('../../.././assets/images/twitter.svg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: calc(15px + 1vmin);
    text-indent: -9999px;
}

li.linkedin a {
    height: 50px;
    width: 50px;
    display: block;
    background-image: url('../../.././assets/images/linkedin.svg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: calc(15px + 1vmin);
    text-indent: -9999px;
}

li.dribbble a {
    height: 50px;
    width: 50px;
    display: block;
    background-image: url('../../.././assets/images/dribbble.svg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: calc(15px + 1vmin);
    text-indent: -9999px;
}

li.insta a {

    height: 50px;
    width: 50px;
    display: block;
    background-image: url('../../.././assets/images/insta.svg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: calc(15px + 1vmin);
    text-indent: -9999px;
}

a.who {
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 50px;
    border-top-left-radius: 50px;
    border-top-right-radius: 0;
    border: none;
    display: block;
    top: 2vh;
    right: 0;
    text-align: center;
    text-decoration: none;
    width: calc(100px + 7vw);
    background-color: #E59767;
    position: fixed;
    padding: 2vmin;
    color: white;
    cursor: pointer;
    font-weight: 600;
    font-size: calc(10px + 1vw);
}

a.who:hover {
    transition: background-color 0.2s ease-in-out 0s; 
    background-color: #E67B3A;
}

